
@import "../lib/bootstrap-5.0.1/scss/_functions";
@import "../lib/bootstrap-5.0.1/scss/_variables";
@import "../lib/bootstrap-5.0.1/scss/_mixins";
@import "_variables";

header {

    position: absolute;
    width: 100%;
    z-index: 1;
    background: linear-gradient(180deg, #d9000d 0%, rgba(255, 255, 255, 0) 100%);
    font-size: .75rem;

    nav {

        .navbar-toggler {
            position: fixed;
            right: 1rem;
            z-index: 1;
        }

        .collapsing {
            transition: none !important;
        }

        .navbar-collapse {
            &.show {

                overflow: hidden;
                transition: height 0.35s ease !important;

                position: fixed !important;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                background-color: $secondary;
                justify-content: end !important;
                padding-top: 3rem;

                .navbar-nav {
                    margin: 0 10rem 2rem 2rem;

                    .nav-item {
                        border-bottom: 2px solid #33688F;
                    }
                }

                .nav-buttons {
                    li {
                        margin: 0 0 1rem 0;
                    }
                }

            }
        }

        .nav-buttons {
            li {
                margin-left: 1rem;
            }
        }

        .nav-item {

            .nav-link {
                &:hover {
                    color: $nav-link-color;
                }
            }

            &:hover {
                background-color: #d9000c;
                border-radius: 0.25rem;
            }
        }

        .nav-btn {
            min-width: 40px;
            min-height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }


    .destek {
        position: fixed;
        top: 80px;
        right: 0;
        font-size: 0.75rem;
        padding: 1rem 4.3125rem 1rem 1.5rem;
        background-color: $secondary;
        border-radius: 3px;
        transition: max-width 0.35s ease-in-out;
        max-width: 140px;

        @include media-breakpoint-down(md) {
            max-width: 90px;
        }

        z-index: 12;

        &:hover {
            max-width: 350px;

            .big-area {
                max-height: 250px;
            }
        }

        .small-area {
            display: flex;
            align-items: center;

            .life-buoy-box {
                position: absolute;
                left: -10px;
                top: 7px;
                font-size: 1.25rem;
            }
        }

        .big-area {
            font-size: 0.625rem;
            max-height: 0;
            width: 260px;
            overflow: hidden;
            transition: max-height 0.35s linear;

            .accordion-item {
                background-color: transparent;
                border: none;
                border-bottom: 1px solid white;
                margin-bottom: 0.5rem;
                padding-bottom: 0.5rem;
            }
        }
    }

}